.m-navi {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  padding: 48px 0 0;
  display: flex;
  flex-direction: column;
  // opacity: 0
  // opacity: .5
  transform: translate3d(0,-100%,0);
  pointer-events: none;
  transition: .25s $ease;

  &.is-active {
    // opacity: 1
    transform: translate3d(0,0,0);
    pointer-events: all; }
  &__wrapper {
    flex: 1 1 auto;
    overflow-y: auto;
    display: flex;
    flex-direction: column; }

  &__menu {
    flex: 1 0 auto;
    margin: 0;
    padding: 0;
    li {
      display: block;
      width: 100%;
      font-size: 14px;
      font-family: $noto;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      border-bottom: 1px solid rgba(black, .1); }
    a {
      display: block;
      transition: color .25s $ease;
      padding: 20px 40px;


      &:hover {
        color: $colorPrimary; } } }

  &__cta {
    padding: 0 40px;
    margin-top: 40px;
    margin-bottom: 40px;
    .btn {
      display: block; } } }

@include r($mobile) {
  .m-navi {
    &__menu {
      a {
        padding: 20px 20px; } }
    &__cta {
      padding: 0 20px; } } }

@include rmin(993) {
  .m-navi {
    display: none !important; } }
