/* hot card */
.h-card {
  position: relative;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  letter-spacing: 0.01em;
  /* slider in slider */
}

.h-card__images {
  flex: 0 0 auto;
  position: relative;
  overflow: hidden;
  font-size: 0;
  border-radius: 4px;
}

.h-card__images-next, .h-card__images-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  z-index: 3;
  color: white;
  cursor: pointer;
  opacity: 0;
  transition: 0.25s ease-in-out;
}

.h-card__images-next {
  right: 15px;
}

.h-card__images-prev {
  left: 15px;
}

.h-card__images:hover .h-card__images-next, .h-card__images:hover .h-card__images-prev {
  opacity: 1;
}

.h-card__image {
  display: block;
  position: relative;
  z-index: 1;
  border-radius: 4px;
  overflow: hidden;
}

.h-card__image img {
  width: 100%;
}

.h-card__image .icon-play {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
  transition: 0.25s ease-in-out;
}

.h-card__image:hover .icon-play {
  transform: scale(1.3);
}

.h-card__content {
  position: relative;
  margin-top: 10px;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}

.h-card__info {
  font-size: 14px;
  line-height: 2;
  display: flex;
  align-items: center;
  color: rgba(42, 42, 42, 0.5);
  letter-spacing: 0.08em;
}

.h-card__info span {
  display: inline-block;
  position: relative;
  padding-right: 12px;
}

.h-card__info span::after {
  display: inline-block;
  position: absolute;
  bottom: 11px;
  right: 3px;
  content: ' ';
  size: 3px;
  background: currentColor;
  border-radius: 50%;
  margin: 0 3px;
}

.h-card__info span:last-child::after {
  display: none;
}

.h-card__name {
  font-family: "Noto Sans", sans-serif;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.03em;
  line-height: 1.33;
  transition: 0.25s ease-in-out;
}

.h-card__name:hover {
  text-decoration: underline;
}

.h-card__addres {
  padding-top: 7px;
  margin-top: auto;
  font-size: 14px;
  line-height: 1.3;
  color: rgba(42, 42, 42, 0.5);
  letter-spacing: 0.08em;
}

.h-card__price {
  margin-top: 15px;
  font-family: "Noto Sans", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.h-card__price span {
  font-weight: 500;
  display: inline-block;
  margin-left: 6px;
}

.h-card__tag {
  position: absolute;
  bottom: -3px;
  right: 0;
  letter-spacing: 0em;
}

.h-card__exclusive {
  color: white;
  font-size: 14px;
  text-align: center;
  padding: 5px 15px 6px;
  background: #404042;
  border-radius: 3px;
}

/* Loader animation */
.h-card.is-loading {
  min-height: 300px;
}

.h-card.is-loading .h-card__images {
  height: 180px;
}

.h-card.is-loading .h-card__images .h-card__images-next, .h-card.is-loading .h-card__images .h-card__images-prev {
  opacity: 0;
}

.h-card.is-loading .h-card__info {
  height: 20px;
  margin-bottom: 10px;
  max-width: 190px;
}

.h-card.is-loading .h-card__name {
  height: 38px;
  max-width: 240px;
}

.h-card.is-loading .h-card__addres {
  height: 32px;
  padding-top: 0;
  margin-top: 15px;
}

.h-card.is-loading .h-card__price {
  height: 25px;
  max-width: 115px;
  margin-top: 15px;
}

.h-card.is-loading .h-card__images, .h-card.is-loading .h-card__info, .h-card.is-loading .h-card__name, .h-card.is-loading .h-card__addres, .h-card.is-loading .h-card__price {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.h-card.is-loading .h-card__images::after, .h-card.is-loading .h-card__info::after, .h-card.is-loading .h-card__name::after, .h-card.is-loading .h-card__addres::after, .h-card.is-loading .h-card__price::after {
  display: inline-block;
  content: ' ';
  position: absolute 0;
  z-index: 1;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: #F6F6F6;
  background: linear-gradient(to right, #F6F6F6 8%, #eaeaea 18%, #F6F6F6 33%);
  background-size: 800px 100%;
}

.h-card.is-loading .h-card__name::after {
  background: #E2E2E2;
  background: linear-gradient(to right, #E2E2E2 8%, #cbcbcb 18%, #E2E2E2 33%);
  background-size: 800px 100%;
}

.h-card.is-loading .h-card__tag {
  display: none;
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0;
  }
  100% {
    background-position: 468px 0;
  }
}

