  }
  .property__tabs {
    margin-top: 0;
  }
  .property__tab {
    display: block !important;
  }
  .property__tab.is-active .property__info {
    display: block;
  }
  .property__tab-mobile {
    display: flex;
  }
  .property__info {
    position: relative;
    z-index: 2;
    margin-top: -1px;
    display: none;
    padding: 20px;
    background: #F6F6F6;
  }
  .property__plan-container {
    padding-left: 25px;
    padding-right: 25px;
  }
  .property__plan-slider {
    margin-bottom: 0;
  }
  .property__plan-slider .swiper-slide {
    flex-direction: column;
    align-items: center;
  }
  .property__plan-wrap {
    flex: 0 0 auto;
    display: block;
    margin-bottom: 30px;
    padding-right: 0;
  }
  .property__plan-wrap:last-child {
    margin-bottom: 0;
  }
  .property__plan-wrap span {
    padding-left: 0;
  }
  .property__map {
    margin-top: 0;
  }
}

/* Gallery */
.gallery__main {
  margin-bottom: 5px;
}

.gallery__main-next, .gallery__main-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 45px;
  z-index: 3;
  cursor: pointer;
  color: white;
}

.gallery__main-next svg, .gallery__main-prev svg {
  filter: drop-shadow(0 5px 5px rgba(177, 177, 177, 0.5));
  transition: filter 0.25s ease-in-out;
}

.gallery__main-next:hover svg, .gallery__main-prev:hover svg {
  filter: drop-shadow(0 4px 8px rgba(177, 177, 177, 0.9));
}

.gallery__main-next {
  right: 35px;
}

.gallery__main-prev {
  left: 35px;
}

.gallery__main-slide {
  position: relative;
  flex: 0 0 auto;
  max-width: 100%;
  font-size: 0;
  display: flex;
  flex-direction: column;
}

.gallery__main-slide a {
  flex: 0 1 auto;
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
}

.gallery__main-slide img {
  width: 100%;
  height: 100%;
}

.gallery__main-video {
  position: absolute;
  z-index: 2;
  bottom: 35px;
  right: 35px;
}

.gallery__thumbs-next, .gallery__thumbs-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 38px;
  z-index: 3;
  cursor: pointer;
  color: white;
}

.gallery__thumbs-next {
  right: 20px;
}

.gallery__thumbs-prev {
  left: 20px;
}

.gallery__thumbs-slide {
  position: relative;
  overflow: hidden;
  flex: 0 0 125px;
  font-size: 0;
}

.gallery__thumbs-slide img {
  width: 100%;
}

.gallery__thumbs-slide::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: #F6BA20;
  opacity: 0;
  transform: translate(0, 3px);
  transition: 0.25s ease-in-out;
}

.gallery__thumbs-slide.swiper-slide-active::after {
  opacity: 1;
  transform: translate(0, 0);
}

@media print, (max-width: 680px) {
  .gallery__main-slide img {
    height: auto;
  }
  .gallery__thumbs {
    display: none;
  }
}

.print-footer, .print-header {
  display: none;
}

@media print {
  .header, .m-navi, .m-search, .footer {
    display: none;
  }
  .print-footer {
    display: block;
  }
  .print-header {
    display: flex;
    align-content: center;
    border-bottom: 1px solid rgba(42, 42, 42, 0.1);
    padding-bottom: 20px;
  }
  .print-header .header__logo .ico {
    font-size: 44px;
  }
  .print-header__contacts {
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .print-header__phone {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 7px;
  }
  .print-header__email {
    font-weight: 700;
    font-size: 14px;
  }
  /* Property */
  .property + .hot {
    display: none;
  }
  .property__mobile-cta {
    display: none;
  }
  .property__name, .property__addres, .property__scope, .property__cta, .property__price, .property__info, .property__tab-mobile {
    padding-left: 0px;
    padding-right: 0px;
  }
  .property__name {
    margin-top: 30px;
  }
  .property__name h1 {
    font-size: 24px;
  }
  .property__addres {
    padding-bottom: 30px;
  }
  .property__section {
    margin-top: 50px;
  }
  .property__section--print-only {
    display: block;
  }
  .property__section--print-only .property__heading {
    display: block;
  }
  .property__info {
    display: block !important;
    border-bottom: 1px solid rgba(42, 42, 42, 0.1);
    padding-top: 0;
  }
  .property__info p {
    margin-top: 10px;
  }
  .property__info-el {
    padding: 3px 0;
  }
  .property__info-el span {
    font-size: 14px;
  }
  .property__tab-mobile {
    margin-top: 0px;
    border-bottom: 0;
  }
  .property__tab-mobile .ico {
    display: none;
  }
  .property__plan-container {
    padding-left: 0px;
    padding-right: 0px;
  }
  .property__plan-container .swiper-wrapper {
    flex-wrap: wrap;
  }
  .property__plan-container .swiper-slide {
    width: 50%;
    flex: 0 0 50%;
    padding-right: 30px;
    margin-top: 30px;
  }
  .property__plan-next, .property__plan-prev {
    display: none;
  }
  .property__map {
    height: auto;
  }
  .property__map #property-map {
    display: none;
  }
  .property__map-static {
    display: block;
  }
  .gallery__main-next, .gallery__main-prev {
    display: none;
  }
  .gallery__main-video {
    display: none;
  }
}
