/* HEADER */
.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background: white;
  &-container {
    border-bottom: 1px solid #E6E6E6; }

  &__wrapper {
    display: flex;
    align-items: center;
    height: 60px;
    color: $fontColor; }

  &__logo {
    position: relative;
    // flex: 0 0 103px
    flex: 0 0 auto;
    font-size: 0;
    padding-right: 30px;
    color: $fontColor;
    transition: .25s $ease;
    .ico {
      font-size: 29px;
      overflow: hidden; }
    &:hover {
      opacity: .8; } }


  &__menu {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 0 auto;
    padding: 0;
    li {
      display: inline-block;
      font-size: 14px;
      font-family: $noto;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      margin: 0 2.5%;
      &.is-active {
        a {
          &::after {
            right: 0;
            background: $colorPrimary;
            box-shadow: 0 0 1px 0 rgba(0,0,0,0.50); } } } }
    a {
      position: relative;
      transition: color .25s $ease;
      padding: 20px 8px;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        top: 0;
        left: 0;
        right: 100%;
        height: 3px;
        background-color: rgba(#404042, .5);
        box-shadow: 0 1px 2px 0 rgba(150,150,150,0.50);
        transition: .25s $ease; }

      &:hover {
        &::after {
          right: 0; } } } }



  &__cta {
    margin-left: auto;
    padding-left: 30px; }
  &__phone {
    position: relative;
    display: inline-block;
    font-family: $noto;
    font-weight: 700;
    font-size: 18px;
    padding: 20px 0;
    letter-spacing: 0.04em;
    transition: .25s $ease;
    // &:hover
    //   color: $colorPrimary
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      top: 0;
      left: 0;
      right: 100%;
      height: 3px;
      background-color: $colorPrimary;
      box-shadow: 0 0 1px 0 rgba(0,0,0,0.50);
      transition: .25s $ease; }
    &:hover {
      &::after {
        right: 0; } } }


  &__search {
    display: none;
    flex: 0 0 40px;
    margin-left: auto;
    padding-top: 2px;
    text-align: right;
    transition: .25s $ease;
    .ico {
      font-size: 22px; }

    &:hover {
      color: $colorPrimary; } }

  &__hamburger {
    display: none; }

  &.is-white {
    background: transparent;
    .header {
      &__logo {
        color: white; }
      &__wrapper {
        color: white; }
      &__menu {
        a::after {
          background-color: rgba(white, .7); } } }
    .hamburger-inner {
      background: white;
      &::before, &::after {
        background: white; } } }

  &.is-fixed {
    transform: translate3d(0,-100%,0);
    background: white;
    transition: .25s $ease; }

  &.is-fixed, &.is-menu-opened {
    background: white;
    .header {
      &__logo {
        color: $fontColor; }
      &__wrapper {
        color: $fontColor; }
      &__menu {
        a::after {
          background-color: rgba($fontColor, .7); } } }
    .hamburger-inner {
      background: $fontColor;
      &::before, &::after {
        background: $fontColor; } } }

  &.is-fixed-visible {
    position: fixed;
    transform: translate3d(0,0,0); }

  &.is-menu-opened {
    transform: translate3d(0,0%,0) !important;
    background: white;
    position: fixed; } }


@include r($wide) {
  .header {
    &__menu {
      li {
        margin: 0 5px; } }
    &__phone {
      font-size: 16px; } } }


@include r($desktop) {
  .header {
    &__wrapper {
      height: 48px; }
    &__logo {
      // flex-basis: 60px
      padding-right: 0;
      .ico {
        font-size: 24px; } }
    &__menu {
      display: none; }
    &__hamburger {
      flex: 0 0 180px;
      margin-right: auto;
      display: block; }
    &__phone {
      flex: 0 0 150px;
      padding: 15px 0; } } }

@include r($tablet) {
  .header {
    &.is-white {
      background: white;
      .header {
        &__logo {
          color: $fontColor; }
        &__wrapper {
          color: $fontColor; }
        &__menu {
          a::after {
            background-color: rgba($fontColor, .7); } } }
      .hamburger-inner {
        background: $fontColor;
        &::before, &::after {
          background: $fontColor; } } }
    &__hamburger {
      flex-basis: 40px;
      max-width: 40px;
      height: 44px; }
    &__cta {
      display: none; }
    &__search {
      display: block; } } }


@include r($mobile) {
  .header {
    &__wrapper {
      padding-left: 5px;
      padding-right: 5px; } } }


/* Header search */
.is-menu-opened {
  .header-search {
    display: none; } }

.header-search {
  background: #F6F6F6;
  border-bottom: 1px solid #E6E6E6;
  transition: .25s $ease;
  &__wrapper {
    height: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    align-items: center; }
  &__input {
    max-width: 100%;
    width: 100%;
    flex: 0 1 auto;
    .ui-group {
      .ico {
        left: 0; }
      input {
        padding-top: 9px;
        padding-bottom: 9px;
        padding-left: 40px;
        background: transparent;
        font-size: 14px;
        &:focus {
          // outline: 1px solid rgba(tomato, .5)
          border-color: transparent; } } } } }

@include r($tablet) {
  .header-search {
    &__wrapper {
      height: 50px;
      padding-top: 5px;
      padding-bottom: 5px; }
    &__input {
      display: none; } } }
