


.blog {
  margin: 70px 0;
  &--inner {
    margin-top: 160px; }
  &__wrapper {
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
    margin-top: 50px; }
  &__grid {
      display: flex;
      margin-left: -15px;
      margin-right: -15px;
      flex-wrap: wrap;
      .blog__col {
        margin-bottom: 15px; } }
  &__col {
    flex: 0 1 33.3%;
    max-width: 33.3%;
    min-width: 1px;
    min-height: 0;
    padding: 15px; }

  &__more {
    text-align: center;
    margin-top: 60px;
    margin-bottom: 30px; } }

.blog-categories {
  margin: 20px -40px 25px;
  padding-bottom: 15px;
  padding-left: 40px;
  padding-right: 40px;
  display: flex;
  align-items: center;
  a {
    flex: 0 0 auto;
    width: auto;
    min-width: 1px;
    display: inline-block;
    font-family: $noto;
    font-weight: 700;
    position: relative;
    transition: color .25s $ease;
    padding: 15px 0px;
    margin-right: 50px;
    letter-spacing: 0.01em;
    color: rgba($fontColor, .7);
    &:first-of-type {
      margin-left: auto; }
    &:last-of-type {
      margin-right: auto; }
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 100%;
      height: 3px;
      background-color: rgba(#404042, .5);
      box-shadow: 0 1px 2px 0 rgba(150,150,150,0.50);
      transition: .25s $ease; }

    &:hover, &.is-active {
      color: $fontColor;
      &::after {
        right: 0; } }

    &.is-active {
      &::after {
        background: $colorPrimary;
        box-shadow: 0 0 1px 0 rgba(0,0,0,0.50); } } } }

@include r($mobile) {
  .blog-categories {
    margin-left: -20px;
    margin-right: -20px;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 10px; } }

.blog-card {
  display: block;
  position: relative;
  &__image {
    display: block;
    position: relative;
    z-index: 1;
    border-radius: 4px;
    overflow: hidden;
    img {
      width: 100%; }
    .icon-play {
      position: absolute;
      top: 20px;
      right: 20px;
      z-index: 2;
      overflow: visible;
      will-change: transform;
      transition: .25s $ease; } }
  &__content {
    display: block;
    position: relative;
    margin-top: 15px; }

  &__category {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    color: rgba($fontColor, .7);
    letter-spacing: .1em; }

  &__name {
    margin-top: 5px;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.4;
    letter-spacing: 0.03em; }

  &__info {
    margin-top: 20px;
    font-size: 14px;
    // font-weight: 700
    color: rgba(#2A2A2A, .6);
    opacity: .5; }

  &:hover {
    .blog-card {
      &__image {
        .icon-play {
          transform: scale(1.3); } }
      &__name {
        text-decoration: underline; } } } }


@include r($desktop) {
  .blog {
    &__wrapper {
      margin-top: 30px;
      margin-left: -40px;
      margin-right: -40px;
      // overflow-x: auto
      padding-left: 25px;
      padding-right: 25px; }
    &__grid {
      .blog__col {
        flex: 0 0 50%;
        max-width: 50%; } }
    &__col {
      flex: 0 0 370px;
      max-width: 370px; }
    &--inner {
      margin-top: 120px; } } }


@include r($mobile) {
  .blog {
    margin-top: 40px;
    margin-bottom: 40px;
    &--inner {
      margin-top: 120px;
      margin-bottom: 80px; }

    &__grid {
      padding-left: 5px;
      padding-right: 5px;
      .blog__col {
        flex: 0 0 100%;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px; } }

    &__wrapper {
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 10px;
      padding-right: 10px; }

    &__more {
      margin-top: 30px; } } }
