// FOOTER
.footer {
  background: #404040 url('../img/footerBg.jpg') no-repeat center;
  background-size: cover;
  padding: 40px 0;
  // bottom: 0
  // left: 0
  // right: 0
  // z-index: -1
  color: $grayColor;
  &__top {
    display: flex;
    align-items: stretch;
    margin-left: -15px;
    margin-right: -15px;
    padding-bottom: 20px; }
  &__credentials {
    font-size: 10px;
    line-height: 2;
    flex: 0 0 200px;
    padding: 0 15px;
    margin-right: auto;
    &--mobile {
      display: none; }

    .ico {
      fill: white;
      font-size: 29px;
      margin-bottom: 5px; }
    p {
      margin: 1em 0; } }

  &__nav {
    flex: 0 1 250px;
    padding: 0 25px;
    &-title {
      position: relative;
      font-size: 14px;
      color: white;
      font-family: $noto;
      font-weight: 700;
      line-height: 1;
      margin-bottom: 17px;
      margin-top: -.1em;
      text-transform: uppercase;
      .ico {
        display: none;
        font-size: 8px;
        position: absolute;
        fill: white;
        top: 50%;
        transform: translateY(-50%); } }
    &-list {
      margin: 0;
      padding: 0;
      line-height: 2;
      li {
        display: block;
        font-size: 12px; }
      a {
        display: inline-block;
        padding: 1px 0;
        transition: .25s $ease;
        &:hover {
          color: white; } } } }
  &__cta {
    margin-left: auto;
    flex: 0 0 180px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-end; }

  &__lang {
    margin-top: -5px;
    width: 100%; }

  &__partners {
    margin-top: auto; }


  &__middle {
    border-top: 1px solid #696969;
    border-bottom: 1px solid #696969;
    padding: 20px 0 40px;
    &-wrap {
      display: flex;
      align-items: center;
      margin: 0 -15px; } }


  &__info {
    font-size: 12px;
    line-height: 2;
    padding: 0 15px;
    max-width: 690px;
    margin-right: auto; }


  &__subscribe {
    position: relative;
    flex: 0 0 400px;
    padding: 0 15px;
    label {
      font-size: 12px;
      line-height: 2; }
    &-form {
      position: relative;
      border: 2px solid #979797;
      border-radius: 3px;
      // overflow: hidden
      input {
        -webkit-apperance: none;
        width: 100%;
        display: block;
        border: 0;
        box-shadow: none;
        padding: 12px;
        color: white;
        background: transparent;
        font-size: 12px 130px 12px 10px;

        &::placeholder {
          color: $grayColor; }
        &:focus {} }

      button {
        position: absolute;
        top: -2px;
        bottom: -2px;
        right: -2px;
        z-index: 2;
        color: white;
        background: #979797;
        font-size: 12px;
        font-family: $noto;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase;
        padding: 15px;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
        transition: .25s $ease;
        &:hover {
          background: $colorPrimary;
          color: white; } } }

    .ui-input__validation {
      position: absolute;
      bottom: -25px;
      left: 15px;
      color: $colorRed; } }



  &__bottom {
    display: flex;
    align-items: center;
    padding: 30px 0 0; }

  &__links {
    font-size: 12px;
    a {
      display: inline-block;
      transition: .25s $ease;
      margin-right: 30px;
      &:hover {
        color: white; }
      &:last-child {
        margin-right: 0; } } }

  &__socials {
    margin-left: auto;
    display: flex;
    align-items: center;
    margin-right: -10px;
    a {
      display: flex;
      align-items: center;
      padding: 5px 10px;
      margin-right: 10px;
      transition: .25s $ease;
      &:hover {
        color: white;
        .icon {
          filter: brightness(0) invert(1); } }
      &:last-child {
        margin-right: 0; } }
    .icon {
      transition: .25s $ease; }
    .ico {
      &-social-fb {
        font-size: 26px; }
      &-social-youtube {
        font-size: 24px; } } } }


@include r($desktop) {
  .footer {
    &__middle {
      padding: 20px 0 30px 0; }
    &__nav {
      display: none;
      &-collapse {
        display: block; } }

    &__info {
      display: none; }
    &__subscribe {
      flex: 0 1 440px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto; } } }

@include r($tablet) {
  .footer {
    background-size: auto;
    background-repeat: repeat;
    &__top {
      flex-wrap: wrap;
      padding-bottom: 0; }
    &__credentials {
      order: 1;
      padding-top: 10px;
      flex-basis: auto;

      p {
        display: none; } }
    &__nav-collapse {
      order: 3;
      flex: 0 0 calc(100% + 50px);
      // flex-basis: 100%
      border-top: 1px solid #696969;
      border-bottom: 1px solid #696969;
      margin-left: -25px;
      margin-right: -25px;
      margin-top: 20px;
      padding: 0;
      .footer__nav-title {
        cursor: pointer;
        padding: 25px 40px;
        margin-bottom: 0;
        transition: .25s $ease;
        &:hover {
          background: #696969; }
        .ico {
          right: 45px;
          display: inline-block;
          transform: translateY(-50%) rotate(180deg);
          transition: .25s $ease; } }
      &.is-opened {
        .footer__nav-title {
          .ico {
            transform: translateY(-50%) rotate(0); } }
        .footer__nav-list {
          display: block; } } }

    &__nav-list {
      display: none;
      padding-left: 40px;
      padding-right: 40px;
      border-top: 1px solid #696969;
      a {
        display: block;
        font-size: 14px;
        padding: 20px 0; }
      li {
        position: relative;
        border-top: 1px solid #696969;
        &::after {
          @include sprite('footer-menu-arrow');
          display: inline-block;
          content: ' ';
          position: absolute;
          right: 4px;
          z-index: 2;
          top: 50%;
          transform: translate(0,-50%); }

        &:first-child {
          border-top: 0; } } }

    &__cta {
      order: 2;
      margin-left: auto; }

    &__partners {
      display: none; }

    &__middle {
      border-top: 0;
      margin-left: -40px;
      margin-right: -40px;
      padding-left: 40px;
      padding-right: 40px;
      &-wrap {
        margin: 0; } }
    &__subscribe {
      padding: 0; } } }


@include r($mobile) {
  .footer {
    padding-bottom: 60px;
    &__middle {
      margin-left: -15px;
      margin-right: -15px;
      padding-left: 15px;
      padding-right: 15px; }
    &__bottom {
      flex-direction: column;
      text-align: center; }
    &__links {
      order: 2;
      flex: 0 0 auto;
      display: flex;
      flex-direction: column;
      a {
        margin-right: 0;
        padding: 10px; } }
    &__socials {
      order: 1;
      flex: 0 0 auto;
      margin-left: 0;
      margin-bottom: 10px;
      a {
        &:last-child {
          margin-right: 10px; } } }

    &__credentials {
      &--mobile {
        display: block;
        order: 3;
        flex: 0 0 auto;
        margin-right: 0;
        font-size: 12px;
        p {
          display: block; } } } } }
